import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"

const Carnet = () => (
  <Layout>
    <SEO title="Carnet" />
        <div class="row">
            <div class="col-lg-7 mx-auto">
                <ul class="timeline">
                    <li class="timeline-item bg-white rounded ml-3 p-4 shadow">
                        <div class="timeline-arrow"></div>
                        <h2 class="h5 mb-0">Cinquième Séance - Site</h2><span class="small text-gray"><i class="fa fa-clock-o mr-1"></i>6 avril, 2020</span>
                        <p class="text-small mt-2 font-weight-light">Durant cette semaine, nous avons régler certaines erreurs sur le site web. On estime qu'environ 80% du travail à été achevé.
                            <br/><br/>
                        </p>
                    </li>
                </ul>
            </div>
            <div class="col-lg-7 mx-auto">
                <ul class="timeline">
                    <li class="timeline-item bg-white rounded ml-3 p-4 shadow">
                        <div class="timeline-arrow"></div>
                        <h2 class="h5 mb-0">Quatrième Séance - Site</h2><span class="small text-gray"><i class="fa fa-clock-o mr-1"></i>30 avril, 2020</span>
                        <p class="text-small mt-2 font-weight-light">Durant cette semaine, nous avons continuer d'optimiser le site web. On estime qu'environ 75% du travail à été achevé.
                            <br/><br/>
                        </p>
                    </li>
                </ul>
            </div>
            <div class="col-lg-7 mx-auto">
                <ul class="timeline">
                    <li class="timeline-item bg-white rounded ml-3 p-4 shadow">
                        <div class="timeline-arrow"></div>
                        <h2 class="h5 mb-0">Troisième Séance - Site Radio</h2><span class="small text-gray"><i class="fa fa-clock-o mr-1"></i>23 Mars, 2020</span>
                        <p class="text-small mt-2 font-weight-light">Lors de cette troisième séance, nous avons mis en place la première page de la partie radio du site de l'Alma
                            <br/><br/>
                        </p>
                    </li>
                </ul>
            </div>
            <div class="col-lg-7 mx-auto">
                <ul class="timeline">
                    <li class="timeline-item bg-white rounded ml-3 p-4 shadow">
                        <div class="timeline-arrow"></div>
                        <h2 class="h5 mb-0">Deuxième Séance - Design</h2><span class="small text-gray"><i class="fa fa-clock-o mr-1"></i>16 Mars, 2020</span>
                        <p class="text-small mt-2 font-weight-light">Lors de cette deuxième séance, nous avons mis en place des maquettes du site web de l'alma qui sont disponible sur la page <Link to="/alma/">Alma</Link>
                            <br/><br/>
                            - Une maquette pour le site de la Radio (Clin d'Oeil FM) comportant chacune des pages du site de la Radio.
                            <br/><br/>
                            - Une maquette pour le site du Cinéma (Cinéma de l'Alma) comportant chacune des pages du site du Cinéma.
                            <br/><br/>
                            Les maquettes ont été faites sur Adobe XD.
                        </p>
                    </li>
                </ul>
            </div>
            <div class="col-lg-7 mx-auto">
                <ul class="timeline">
                    <li class="timeline-item bg-white rounded ml-3 p-4 shadow">
                        <div class="timeline-arrow"></div>
                        <h2 class="h5 mb-0">Première Séance - Découverte du Projet</h2><span class="small text-gray"><i class="fa fa-clock-o mr-1"></i>9 Mars, 2020</span>
                        <p class="text-small mt-2 font-weight-light">Lors de cette première séance, nous avons réfléchis sur la manière la plus efficace afin de produire un site de qualité pour l'association Alma. Il fût décidé de partir de zéro, et de créer notre propre CMS (Gestionnaire de contenu) pour permettre à l'administrateur d'éventuellement modifier le contenu de chacune des pages du site de l'Alma. Ayant pas mal de connaisance en développement web, j'ai décidé avec mon équipe de partir sur du Gatsby un framework Javascript qui permet de créer des sites statiques et basé sur l'engin ReactJS. Le site se décompenserai de la manière suivante: <br/><br/><strong>1. Le site en lui même (que l'on trouverai à l'adresse <a>https://almaclindoeilfm.org/</a>):</strong>
                            <br/><br/>
                            - Une Page Principale qui permettrai de se diriger ou bien vers les pages de la Radio Clin d'oeil FM ou alors aux pages du Cinéma de l'Alma.
                            <br/><br/>
                            - Plusieurs pages pour la Radio Clin d'oeil FM (que l'on trouverai en rajoutant /radio/ à l'adresse de base ou en cliquant sur un bouton sur la page d'accueil).
                            - Plusieurs pages pour le Cinéma de l'Alma (que l'on retrouverai encore une fois de la même manière: /cinema/ ou sur le bouton).
                            <br/><br/>
                            <strong>Une interface de Gestion Administratrative:</strong>
                            <br/><br/>
                            - Une interface avec mot de passe qui permettrai à l'utilisateur de modifier le contenu de chacune des pages du site web (images / textes / liens)
                        </p>
                    </li>
                </ul>
            </div>
        </div>
  </Layout>
)

export default Carnet
